import { useReducer, createContext } from "react";
import { cartReducer, cartInitialState } from "../reducer/Cart";

export const CartContext = createContext();

function useCartReducer() {
  const [state, dispatch] = useReducer(cartReducer, cartInitialState);

  const addToCart = (product) =>
    dispatch({
      type: "ADD_TO_CART",
      payload: product,
    });

  const removeFromCart = (product) =>
    dispatch({
      type: "REMOVE_FROM_CART",
      payload: product,
    });

  const clearCart = () => dispatch({ type: "CLEAR_CART" });
  const updateCart = (product) => dispatch({type: "UPDATE_CART", payload: product});

  return { state, addToCart, removeFromCart, clearCart, updateCart };
}


export function CartProvider({ children }) {
  const { state, addToCart, removeFromCart, clearCart, updateCart } = useCartReducer();

  return (
    <CartContext.Provider
      value={{
        cart: state,
        addToCart,
        removeFromCart,
        clearCart,
        updateCart
      }}
    >
      {children}
    </CartContext.Provider>
  );
}
